@charset "UTF-8";


/* /cs/square/qustnr/QustnrRegist.html */
.QustnrRegist{
    .regist_wrap{
        width:1280px; margin:0 auto; padding:120px 20px; border-top:1px solid $gray4; border-bottom:1px solid $gray6;
        .txt_wrap{width:400px; margin:0 auto; padding:110px 0 0; text-align:center; background:url("/img/cs/square/icon_regist.jpg") 50% 0 no-repeat;}
        strong{
            color:$blue1; font-weight:$bold;
            @include fontRul(36px);
        }
        p{
            color:$gray4; font-weight:$bold; margin:30px 0 70px;
            @include fontRul(20px);
        }
        a{
            height:50px; padding:0 60px; font-size:16px; font-weight:$bold; line-height:50px; color:$gray2; border:1px solid $gray5; display:inline-block;
            @include borderRadius(50px);
            @include transition(all);
        }
        a:hover{background-color:$primary; color:$white;}
    }
}


/* /cs/square/ctznBoard/CtznBoardList02.html */
.CtznBoardList{
    .report_info_wrap{
        width:1280px; margin:0 auto; border-top:1px solid $gray6; border-bottom:1px solid $gray6; padding:50px;
        background:url("/img/cs/square/icon_report_info.jpg") 97% 50% no-repeat;
        @include boxSizing;
        ul{
            margin:0 0 20px;
            li{
                color:$blue3; font-weight:$bold;
                @include fontRul(16px);
                &:before{
                    width:4px; height:4px; content:""; display:inline-block; background-color:$blue3; vertical-align:middle; margin:0 10px 0 0;
                    @include borderRadius(4px);
                }
            }
        }
        a{
            width:180px; height:50px; display:inline-block; font-size:16px; letter-spacing:-.5px; line-height:50px; color:$white; font-weight:$bold; text-align:center; margin:0 15px 0 0;
            border:1px solid transparent;
            @include borderRadius(90px);
            @include transition(all);
            @include boxSizing;
            &.complaints_btn{background-color:$gray3;}
            &.complaints_btn:hover{background-color:$white; color:$gray3; border:1px solid $gray3;}
            &.propose_btn{background-color:$blue2;}
            &.propose_btn:hover{background-color:$white; color:$blue2; border:1px solid $blue2;}
        }
    }
}